.header {
    margin: 19px 0 0 0;
}

.navbar {
    border-left: 2px solid $secondary-color;
    min-height: 63px;
    position: relative;
    padding: 13px 0 13px 15px;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &:before {
        position: absolute;
        left: 0;
        top: 0;
        border-top: 2px solid $secondary-color;
        width: 40px;
        content: "";
    }

    &:after {
        position: absolute;
        left: 0;
        bottom: 0;
        border-bottom: 2px solid $secondary-color;
        width: 40px;
        content: "";
    }

    &-brand {
        display: flex;
        flex-direction: column;
        flex-basis: 33.3%;

        &__title {
            font-weight: 700;
            font-size: 28px;
            line-height: 34px;
        }

        &__subtitle {
            font-family: "Work Sans";
            font-weight: 400;
            font-size: 11px;
            line-height: 13px;
            opacity: 0.6;
        }
    }

    &-menu {
        display: flex;
        flex-basis: 33.3%;

        &__item {
            margin-right: 40px;

            &:last-child {
                margin-right: 0;
            }
        }

        &__link {
            color: $color-white;
            text-decoration: none;
            transition: color .5s;

            &:hover {
                color: $secondary-color;
            }
        }
    }

    &-menu--right {
        display: flex;
        flex-basis: 33.3%;
        justify-content: flex-end;
    }
}

.dropdown {
    position: relative;

    &-menu {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(25px);
        animation: dropdown .5s;
        border: 1px solid #979797;
        background: $primary-color;
        width: 247px;


        &.show {
            display: block;
        }
    }

    &-btn {
        &:after {
            content: "";
            position: absolute;
            top: 6px;
            right: -14px;
            width: 0;
            height: 0;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
            border-bottom: 8px solid $color-white;
            transition: transform .5s;
        }

        &.show:after {
            transform: rotate(180deg);
        }
    }

    &-item {
        padding: 10px 0;
        display: flex;
        justify-content: center;
        transition: background .1s;

        &:hover {
            background: $secondary-color;

            .dropdown-link {
                color: $primary-color;
            }
        }
    }

    &-link {
        color: $color-white;
        text-decoration: none;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        transition: color .1s;
    }
}

@keyframes dropdown {
    from {
        transform: translateY(15px);
    }

    to {
        transform: translateY(25px);
    }
}